<template>
    <div>
        <nav-bar>
            <div slot="left" class="back" @click="bakcClick">
                <img src="~assets/img/common/back.svg" alt="" srcset="">
            </div>
            <div slot="center" class="title">
                <div v-for="item,index in titles" :key="index" class="title-item" :class="{active:index===currentIndex}"
                    @click="titleClick(index)">
                    {{item}}
                </div>
            </div>
        </nav-bar>
    </div>
</template>

<script>
    import NavBar from 'components/common/navbar/Navbar.vue'

    export default {
        name: "DetailNavBar",
        components: {
            NavBar
        },
        data() {
            return {
                titles: ['商品', '参数', '评论', '推荐'],
                currentIndex: 0
            }
        },
        methods: {
            titleClick(index) {
                this.currentIndex = index;
                this.$emit('titleClick', index)
            },
            bakcClick() {
                this.$router.back()
            }
        }
    }
</script>

<style scoped>
    .title {
        display: flex;
    }

    .title-item {
        flex: 1;
        font-size: 13px;
    }

    .active {
        color: var(--color-high-text)
    }

    .back img {
        margin-top: 12px;
    }
</style>